// Datos de los módulos de secundaria
const secondaryModules = [
    { name: 'Módulo A', professors: [] },
    { name: 'Módulo B', professors: [] }
];

// Función para cargar los módulos de secundaria
function cargarModulosSecundaria() {
    const secondaryModulesContainer = document.getElementById('secondary-modules');
    secondaryModules.forEach(module => {
        const moduleElement = document.createElement('div');
        moduleElement.textContent = `Secundaria: ${module.name}`;
        moduleElement.classList.add('module-item');
        moduleElement.setAttribute('data-module-name', module.name);
        moduleElement.addEventListener('drop', drop);
        moduleElement.addEventListener('dragover', allowDrop);
        secondaryModulesContainer.appendChild(moduleElement);
    });
}

// Datos de los profesores de secundaria
const secondaryProfessors = ['Paco', 'Luis'];

// Función para cargar los profesores de secundaria
function cargarProfesoresSecundaria() {
    const secondaryProfessorsContainer = document.querySelector('.secondary-professors');
    secondaryProfessors.forEach(professor => {
        const professorElement = document.createElement('div');
        professorElement.textContent = professor;
        professorElement.classList.add('professor', 'secondary');
        professorElement.draggable = true;
        professorElement.addEventListener('dragstart', dragStart);
        professorElement.addEventListener('dragend', dragEnd);
        secondaryProfessorsContainer.appendChild(professorElement);
    });
}

// Función para permitir el soltar
function allowDrop(event) {
    event.preventDefault();
}

// Función para soltar el elemento
function drop(event) {
    const moduleName = event.target.getAttribute('data-module-name');
    const professorName = event.dataTransfer.getData('text');
    
    // Verificar si el profesor ya está asignado a este módulo
    const module = secondaryModules.find(module => module.name === moduleName);
    if (module && !module.professors.includes(professorName)) {
        module.professors.push(professorName);
        event.target.textContent += `\n${professorName}`;
    }
}

// Definir la función cuando inicia el arrastre
function dragStart(event) {
    event.dataTransfer.setData('text', event.target.textContent.trim());
}

// Definir la función cuando termina el arrastre
function dragEnd() {
    // No es necesario hacer nada en este caso
}

cargarModulosSecundaria();
cargarProfesoresSecundaria();
